	module.exports = {"data":{"markdownRemark":{"html":"<h3>Security Inbox</h3>\n<p>We’ve redesigned the security inbox to enable faster bug processing for programs. The new inbox enables programs to open reports inline so you don’t have to click backward or forward to navigate between reports.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-01a87.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 64.07142857142857%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"aug_2014_security_inbox\"\n        title=\"\"\n        src=\"/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-b0c9e.jpg\"\n        srcset=\"/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-f36df.jpg 125w,\n/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-4a0b6.jpg 250w,\n/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-b0c9e.jpg 500w,\n/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-8a2fd.jpg 750w,\n/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-219ba.jpg 1000w,\n/static/aug_2014_security_inbox-476aea11952ebc84cf9c39562cad0a2e-01a87.jpg 1400w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Dashboard</h3>\n<p>The new dashboard enables insight into your security response posture. This enables programs to be on top of response time, stale issues, pending disclosures and more.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-01a87.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 44.07142857142857%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"aug_2014_bulk_actions\"\n        title=\"\"\n        src=\"/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-b0c9e.jpg\"\n        srcset=\"/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-f36df.jpg 125w,\n/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-4a0b6.jpg 250w,\n/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-b0c9e.jpg 500w,\n/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-8a2fd.jpg 750w,\n/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-219ba.jpg 1000w,\n/static/aug_2014_dashboard-397cbc0560dbceb9cfc5af871d8bcc4f-01a87.jpg 1400w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Bulk Actions</h3>\n<p>We improve our bulk actions functionality so that it’s easier to apply the same action to multiple reports with a single click.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-01a87.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 54%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"aug_2014aug_2014_bulk_actions\"\n        title=\"\"\n        src=\"/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-b0c9e.jpg\"\n        srcset=\"/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-f36df.jpg 125w,\n/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-4a0b6.jpg 250w,\n/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-b0c9e.jpg 500w,\n/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-8a2fd.jpg 750w,\n/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-219ba.jpg 1000w,\n/static/aug_2014_bulk_actions-519d07ee57a30917537fb89ca8fa6629-01a87.jpg 1400w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Keyboard Shortcuts</h3>\n<p>We introduce keyboard shortcuts to make the workflow more efficient with a faster navigation.</p>\n<h3>Search</h3>\n<p>Our new inbox filtering search functionality enables programs and hackers to quickly target the bug they're looking for without having to scroll through their inbox.</p>\n<h3>Integrations</h3>\n<p>We introduce these new integrations with HackerOne: <li>MantisBT</li><li>Bugzilla</li><li>Jira</li><li>Phabricator</li></p>","frontmatter":{"path":"/changelog/2014/august","title":"August 2014"}}},"pathContext":{}}

